@import "../variables";

.documents {

    .path-link a{
        text-decoration: none;
        color: inherit;

        &:hover {
            color: black;
        }
    }

    .dropzone {
        @include flex;
        border-width: 2px;
        border-color: transparent;
        border-style: dashed;
        border-radius: 5px;
        margin-top: 5px;
        padding: 2px;
        padding-bottom: 50px;
        position: relative;

        &.active {
            border-color: steelblue;
            background-color: rgba(10,158,200,0.1);
        }

        .message {
            text-transform: uppercase;
            color: rgba(0,0,0,0.3);
            font-weight: bold;
            font-size: 50px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

  .loading {
    height: 200px;
    max-height: 100vh;
    @include display-flex;
    @include align-items(center);
    @include justify-content(center);
    img {
      height: 80px;
    }
  }
  .button-import {
    input {
      opacity: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0px;
      right: 0px;
      bottom: 0px;
      top: 0px;
      z-index: -1;
      pointer-events: none;

    }
  }
  .document-list {
    @include display-flex;
    &.view-mode-grid {
      @include flex-direction(row);
      @include justify-content(flex-start);
      @include flex-wrap(wrap);
      margin: 0px -16px;


      .document {
        max-width: 140px;
        margin: 16px;
        position: relative;

        .document-date {
          display: none;
        }
        .document-icon-container {
          width: 50vw;
          height: 50vw;
          max-width: 140px;
          max-height: 140px;
          background-color: #f6f6f6;
          border: solid 1px #cccccc;
          overflow: hidden;

          cursor: pointer;
          margin-bottom: 16px;
          @include display-flex;
          @include align-items(center);
          @include justify-content(center);
          .document-icon-content {
            position: relative;
            width: 70%;

            input[type="checkbox"] {
              display: none;
              position: absolute;
              top: -10px;
              left: -15px;
            }
            .document-icon {
              display: block;
              width: 100%;
              margin: 0px auto;
            }
          }
        }

        .document-name {
          font-family: $fontFamily;
          font-size: 14px;
          font-weight: 500;
          letter-spacing: 1.2px;
          text-align: center;
          color: #32363f;
          white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        input[type="checkbox"] {
          display: none;
          position: absolute;
          top: 10%;
          left: 10%;
          z-index: 10;
        }

        &:hover, &.selected {
          input[type="checkbox"] {
            display: block;
          }
          .document-icon-container {
            //background-color: lighten(#f6f6f6, 5%);
            border: solid 1px #333;

            .document-icon-content {

            }
          }
        }

        &.selected {
          input[type="checkbox"] {
            display: block;
          }

          .document-icon-container {
            background-color: #334254;
            color: #FFF;
          }
          .document-name {
            background-color: #334254;
            color: #FFF;
          }
          .document-name {
            color: #FFF;
          }
        }
      }

      @media screen and (max-width: 767px) {
        @include justify-content(center);

        .document {
          max-width: 40vw;

          .document-icon-container {
            max-width: 40vw;
            max-height: 40vw;
          }
        }
      }

    }
    &.view-mode-line {
      @include flex-direction(column);
      .document {
        cursor: pointer;
        margin: 16px 0 0 0;
        padding: 16px;
        background: #F6F6F6;
        border: 1px solid #CCCCCC;

        @include display-flex;
        @include align-items(center);

        &.selected {
          background-color: #334254;
          .document-icon-container {
            color: #FFF;
          }
          .document-name {
            color: #FFF;
          }
          .document-date {
            color: #FFF;
          }
        }

        .document-name {
          @include flex(1);
        }

        .document-date {
          font-size: 14px;
          color: #32363F;
          letter-spacing: 1.17px;
          font-weight: 100;
        }
        .document-icon-container {
          width: 60px;
          height: 51px;
          margin: 0px 16px;

          .document-icon {
            width: 100%;
            height: auto;
            display: block;
          }
        }
      }
    }
  }

}



