@import "../variables";

.recalls {
    .section-content {
        padding: 15px 0;
    }

    .recall {
        @include display-flex;
        @include flex-direction(row);
        @include align-items(center);
        @include justify-content(space-between);
        margin: 16px 0;
        background-color: #f6f6f6;
        border: solid 1px #cccccc;
        padding: 20px;
        font-weight: 300;
        cursor: pointer;

        .recall-checkbox {
            i {
                font-size: 25px;
            }

            .ion-android-checkbox-outline-blank {
                display: block;
            }

            .ion-android-checkbox-outline {
                display: none;
            }

            &.checked {
                .ion-android-checkbox-outline-blank {
                    display: none;
                }

                .ion-android-checkbox-outline {
                    display: block;
                }
            }
        }

        .recall-icon {
            width: 30px;
            font-size: 40px;
            margin: 0 35px;
        }

        .recall-name {
            @include flex(1);
            text-align: left;
            font-weight: normal;
            padding-left: 25px;
        }

        .recall-deadline {
            @include flex(1);
            text-align: right;
        }

        .recall-alert {
            @include flex(1);
            text-align: right;
            margin-right: 30px;
        }

        &.active,
        &:hover {
            border: solid 1px #4a4a4a;

            .recall-checkbox {
                opacity: 1;
            }
        }
    }
    @media screen and (max-width: 767px) {
        .recall {
            margin: 0 -16px;
            border-left: none;
            border-right: none;

            &:not(:nth-child(2)) {
                border-top: none;
            }

            .recall-checkbox {
                display: none;
            }

            .recall-deadline {
                display: none;
            }

            .recall-alert {
                display: none;
            }

            .recall-icon {
                display: none;
            }
        }
    }

    .edit-modal {
        margin: -16px;
        border: 1px solid #DDD;

        .edit-modal-header {
            background-color: #F6F6F6;
            height: 88px;
            @include display-flex;
            @include align-items(center);
            @include justify-content(space-between);
            border-bottom: 1px solid #DDD;
            padding: 16px;

            h1 {
                @include flex(1);
                font-size: 24px;
                color: #32363F;
                letter-spacing: 1px;
                line-height: 40px;
                font-weight: 500;
            }

            .button {
                margin: 0 16px;
            }
        }

        .edit-modal-content {
            padding: 16px;

            h3 {
                font-size: 16px;
                color: #95999D;
                letter-spacing: 1.14px;
                line-height: 24px;
                margin: 0 0 10px;
                font-weight: lighter;
            }

            p {
                font-size: 16px;
                color: #4A4A4A;
                letter-spacing: 1.14px;
                line-height: 24px;
                margin: 0 0 32px;
                font-weight: lighter;
            }

            p + p {
                margin-top: -32px;
            }

            a,
            a:visited {
                color: #4A4A4A;
            }
        }

        &.edit-modal-form {
            .form-group,
            .form-group-inline {
                > label {
                    /* Prnom: */
                    font-size: 16px;
                    color: #95999D;
                    letter-spacing: 1.14px;
                    line-height: 24px;
                    margin-bottom: 16px;
                    display: block;
                }

                > input,
                select {
                    background: #FFFFFF;
                    border: 1px solid #9B9B9B;
                    border-radius: 2px;
                    font-size: 16px;
                    color: #4A4A4A;
                    letter-spacing: 1.14px;
                    line-height: 24px;
                    display: block;
                    max-width: 400px;
                    width: 100%;
                    padding: 10px 16px;
                    margin-bottom: 16px;
                    box-sizing: border-box;
                    height: 46px;
                }

                select:after {
                    content: $ionicon-var-ios-arrow-down;
                    font-family: $ionicons-font-family;
                    display: block;
                    position: absolute;
                    font-size: 25px;
                    right: 16px;
                }

                .form-group-inline {
                    @include display-flex;
                    @include flex-direction(row);
                    @include align-items(center);
                    @include justify-content(space-between);

                    > input,
                    select {
                        margin-right: 16px;

                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
        }
        @media screen and (max-width: 767px) {
            .edit-modal-header {
                .more-button,
                .save-button,
                .update-button {
                    display: none;
                }
            }
        }
    }
}



